/*text formating*/
.main_text_box{
  flex: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font: 400 16px "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
}
.text_box{
  width: 98%;
  max-width: 50em;
  font: 400 16px "Montserrat", sans-serif;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  margin: 0;
  padding: 10px;
  outline: 5px solid rgba(0,0,0, 0.2);
}
.text_box .gallery{
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}
.text_box .gallery img{
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0.5%;
}
.text_box .gallery .nested_gallery{
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 0;
  padding: 0;
}
.text_box .gallery .nested_gallery img{
  width: 50%;
  height: auto;
  margin: 0;
  padding: 0.5%;
}
/*text formating*/
.text_box h1 { 
  font: 700 20px "Montserrat", sans-serif;
  color: #98301b;
  text-indent: 0;
  margin: 10px 0 10px 0;
  padding: 0 1% 0 1%;
}
.text_box b { 
  font: 700 16px "Montserrat", sans-serif;
  color: #878787;
  text-indent: 0;
  margin: 0;
  padding: 0;
}
.text_box p { 
  font: 400 16px "Montserrat", sans-serif;
  color: #878787;
  text-indent: 0;
  margin: 5px 0 5px 0;
  padding: 0 1% 0 1%;
}
.text_box small { 
  display: block;
  text-align: right;
  font: 400 12px "Montserrat", sans-serif;
  color: #e0674f;
  text-indent: 0;
  margin: 0;
  padding: 0 1% 0 1%;
}
/*table formating*/
table {
  vertical-align: top;
  border: 0px;
  font: 400 16px/1.5 "Montserrat", sans-serif;
  text-align: left;
  color: #878787;
  margin: 0 1% 0 1%;
}
table tbody tr td {
  vertical-align: top;
  min-width: 20px;
}

/*----------------------modal stuff----------------------*/

.modal_image {  /*<-- image is the thumbnail change latter to fit*/
  cursor: pointer;
  display: block;
  position: relative;
  width: 97%;
  margin: 0 1.5% 3% 1.5%;
  z-index: 1;
}
.modal_image:hover {
  filter:  brightness(0.6);
  transition: .3s ease;
}
/*----------------modal gallery----------------*/

/* background */

.modal {
  display: none;
  position: fixed; 
  z-index: 3;
  padding: 0;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #000000; /* Fallback color */
  background-color: rgba(0,0,0,0.9);
  vertical-align: middle;
}

	/* Modal Content (image) */

.modal_content {
  margin: auto;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: scale-down;

  /*to make it harder to download*/
  /* also add draggable="false"; to it in html */
  /* menu has been desactivated with js */
}
	/* Caption of Modal Image */

#Caption { 
  display: block;
  position: fixed;
  top: 10px;
  right: 190px;
  background: white; /*fallback*/
  background-color: rgba(255, 255, 255, 0.6);
  padding: 10px;
  text-align: right;
  font: 400 16px/1 "Montserrat", sans-serif;
  color: #000000;
}
/* Add Animation */
.modal_content, #caption{  
  -webkit-animation-name: zoom;
  -webkit-animation-duration: 0.6s;
  animation-name: zoom;
  animation-duration: 0.6s;
} 

@-webkit-keyframes zoom {
  from {-webkit-transform:scale(0)} 
  to {-webkit-transform:scale(1)}
}

@keyframes zoom {
  from {transform:scale(0)} 
  to {transform:scale(1)}
}

/* all the control Button */

.close {
  position: fixed;
  top: 10px;
  right: 10px;

  height: 50px;
  width: 50px;
  background-color: #ffffff;
  color: #000000;
  font-size: 35px;
  padding: 5px;
  font-weight: bold;
  text-align: center;

  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10+ and Edge */
  user-select: none; /* Standard syntax */
}

.close:hover,
.close:focus {
  color: #98301b;
  text-decoration: none;
  cursor: pointer;
}

.prev{
  cursor: pointer;
  position: fixed;
  right: 130px;
  top: 10px;

  height: 50px;
  width: 50px;
  padding: 10px;
  background-color: #ffffff;
  color: #000000;
  font-weight: bold;
  font-size: 20px;
  text-align: center;

  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10+ and Edge */
  user-select: none; /* Standard syntax */
}
.next {
  cursor: pointer;
  position: fixed;
  right: 70px;
  top: 10px;

  height: 50px;
  width: 50px;
  padding: 10px;
  background-color: #ffffff;
  color: #000000;
  font-weight: bold;
  font-size: 20px;
  text-align: center;

  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10+ and Edge */
  user-select: none; /* Standard syntax */
}
.info {/*hidden for now*/
  display: none;
  cursor: pointer;
  position: fixed;
  bottom: 15vh;
  left: 2vw;
  width: auto;
  background-color: #ffffff;
  color: #000000;
  font-weight: bold;
  font-size: 30px;
  transition: 0.6s ease;

  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10+ and Edge */
  user-select: none; /* Standard syntax */
}

/* On hover, add a black background color */
.prev:hover,
.next:hover,
.info:hover{
  color: #737373;
}


